import request from '../request' // axios
import base from '../base'
import qs from 'qs'

export const OutdoorTestApi = {
  queryOutHome: (params) => {
    return request('post', `${base.sq5G}/es/buss/5g/queryOutHome`, params,{})
  },
  deleteOutHome:(params) => {
    return request('post', `${base.sq5G}/es/buss/5g/deleteOutHome`, qs.stringify(params),{})
  },
  exportOutHomeTest:(params) => {
    return request('post', `${base.sq5G}/es/buss/exportOutHomeTest`, qs.stringify(params),{responseType:'blob'})
  },
  //map
  queryMainLog:(params) => {
    return request('post', `${base.sq5G}/es/buss/5g/queryMainLog`,params,{})
  },
  //
  queryList:(params) => {
    return request('post', `${base.sq5G}/es/queryList`,params,{})
  },
  //
  queryMainService:(params) => {
    return request('post', `${base.sq5G}/es/buss/5g/queryMainService`,qs.stringify(params),{})
  },
  //PCI
  queryNearPoint:(params) => {
    return request('post', `${base.sq5G}/es/buss/5g/queryNearPoint`,qs.stringify(params),{})
  },
  //SIM  
  boundingBoxQuery:(params) => {
    return request('post', `${base.sq5G}/es/buss/5g/boundingBoxQuery`,params,{})
  },
  //SIM()
  // scrollQuerySimGc:(params) => {
  //   return request('post', `${base.sq5G}/es/buss/scrollQuerySimGc`,params,{})
  // },
  distinctGcLonLat:(params) => {
    return request('post', `${base.sq5G}/es/buss/5g/distinctGcLonLat`,params,{})
  },
  //
  saveLegend:(params) =>{
    return request('post', `${base.sq5G}/buss/testConfig/edit`,params,{})
  },
  queryLegend:(params)=>{
    return request('post', `${base.sq5G}/buss/testConfig/query`,qs.stringify(params),{})
  },
  //Logcsv
  exportOutHomeCsv:(params)=>{
    return request('post', `${base.sq5G}/es/buss/5g/exportOutHomeCsv`, qs.stringify(params),{responseType:'blob'})
  },
  //
  queryAnalyzeData:(params)=>{
    return request('post', `${base.sq5G}/buss/analyze/queryAnalyzeData5g`, qs.stringify(params),{})
  },
  //Log
  importOutHomeLog:(params)=>{
    return request('post', `${base.sq5G}/es/buss/5g/importOutHomeLog`, params,{})
  },
  // 
  memoryConfigQuery:(params)=>{
    return request('post', `${base.sq5G}/buss/memoryConfig/query`, params,{})
  },
  // 
  memoryConfigEdit:(params)=>{
    return request('post', `${base.sq5G}/buss/memoryConfig/edit`, qs.stringify(params),{})
  },
  // CI
  queryByCi:(params)=>{
    return request('post', `${base.sq5G}/es/buss/5g/queryGcByCi`, qs.stringify(params),{})
  },
  // CI
  queryNetLine:(params)=>{
    return request('post', `${base.sq5G}/es/buss/5g/queryCellNetLine`, qs.stringify(params),{})
  },
  // 
  querySixPane:(params)=>{
    return request('post', `${base.sq5G}/es/buss/5g/querySiXWindowService`, qs.stringify(params),{})
  },
  // 
  queryLine:(params)=>{
    return request('post', `${base.sq5G}/buss/memoryConfig/queryLine`, qs.stringify(params),{})
  },
  // 
  editLine:(params)=>{
    return request('post', `${base.sq5G}/buss/memoryConfig/editLine`, qs.stringify(params),{})
  },
  // 
  lineData:(params)=>{
    return request('post', `${base.sq5G}/es/buss/5g/queryAnalyzeLineData`, qs.stringify(params),{})
  }
}



