// 
export function GetDateStr(AddDayCount) {
  var dd = new Date();
  dd.setDate(dd.getDate() + AddDayCount);//AddDayCount

  var year = dd.getFullYear(); //
  var mon = (dd.getMonth() + 1) < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1;//
  var day = (dd.getDate()) < 10 ? '0' + (dd.getDate()) : dd.getDate(); //
  return year + "-" + mon + "-" + day;
}


//
export function timestampToTime(timestamp) {
  var date;
  if (timestamp.length === 10) {
    date = new Date(timestamp * 1000);//10*1000131000
  } else {
    date = new Date(timestamp);//10*1000131000
  }
  var Y = date.getFullYear() + '-';
  var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
  var D = date.getDate() + ' ';
  var h = date.getHours() + ':';
  var m = date.getMinutes() + ':';
  var s = date.getSeconds();
  return Y + M + D + h + m + s;
}

//
export function timestampToSeconds(timestamp) {
  var date = new Date(timestamp);
  var Hour = date.getHours();
  var THour = Hour < 10 ? "0" + Hour : String(Hour);
  var Minute = date.getMinutes();
  var TMinute = Minute < 10 ? "0" + Minute : String(Minute);
  var Second = date.getSeconds();
  var TSecond = Second < 10 ? "0" + Second : String(Second);
  var Millisecond = date.getMilliseconds();
  return THour + ':' + TMinute + ':' + TSecond + ':' + Millisecond;
}



